@media (max-width: 576px) {
  .project-desc {
    font-size: 14px;
  }
  .project-tools {
    font-size: 12px;
  }
}

.description {
  list-style-type: disc;
  font-weight: 100;
  font-size: 18px;
  color: #646f79;
  opacity: 0.8;
}

@media (max-width: 768px) {
  .description {
    font-size: 16px;
  }
}

@media (max-width: 576px) {
  .description {
    font-size: 14px;
  }
}

